body{
    margin: 0;
    padding: 0;
}
.App{
    padding: 60px 80px;
    min-height: calc(100vh - 120px);
    background-color: #f3f6f4;
}
input:focus {
    outline: none;
}
*{
    font-family: 'Montserrat', sans-serif;
}

@media (max-width: 410px) {
    .App {
        padding: 60px 20px;
    }
}